import { useState } from 'react'
import { useImmer } from 'use-immer'
import { NavLink, useNavigate } from 'react-router-dom'
import styled from 'styled-components'
import { registerAccount } from '../api/users'
// import { login } from '../api/users'
// logo
import { Logo } from '../assets/icons'
// button
import { AuthButton } from './common/button.styled'
// input 
import { AuthInput } from './AuthInput'

// Container
const StyledContainer = styled.div`
  max-width: 356px;
  padding: 0;
`
// Header
const StyledHeader = styled.div`
  svg {
    margin-bottom: 24px;
  }
`
// Body
const StyledBody = styled.div`
  width: 100%;
  gap: 32px;
  margin: 40px 0;

  label,
  input {
    padding: 0 10.55px;
  }
  label {
    padding-top: 6px;
  }
`

// Footer
const StyledFooter = styled.div`
  button {
    margin-bottom: 22px;
  }
`

// Link
const StyledLinkWrapper = styled.ul`
  width: 100%;
  gap: 12px;

  span a {
    line-height: 24px;
  }

  a {
    text-decoration: none;
    color: var(--primary);
    border-bottom: 1px solid var(--primary);
  }
`
const inputs = [
  {
    label: '',
    placeholder: '',
    type: 'text',
    status: '',
    errorText: '',
    value: '',
  },
  {
    label: '',
    placeholder: '',
    type: 'text',
    status: '',
    errorText: '',
    value: '',
  },
  {
    label: 'Email',
    placeholder: ' Email',
    type: 'text',
    status: '',
    errorText: '',
    value: '',
  },
  {
    label: '',
    placeholder: '',
    type: 'password',
    status: '',
    errorText: '',
    value: '',
  },
  {
    label: '',
    placeholder: '',
    type: 'password',
    status: '',
    errorText: '',
    value: '',
  },
]

export default function Regist({ openModal }) {
  const [account, setAccount] = useState('') //  value
  const [username, setUsername] = useState('') //  value
  const [email, setEmail] = useState('') // Email value
  const [password, setPassword] = useState('') //  value
  const [checkPassword, setCheckPassword] = useState('') //  value
  const [inputList, UpdateInputList] = useImmer(inputs)
  const navigate = useNavigate()
  //  input 
  function setInput(num, status, errorText) {
    UpdateInputList((draft) => {
      draft[num].status = status
      draft[num].errorText = errorText
    })
  }

  // input
  function handleChange(event) {
    const target = event.target
    if (target.placeholder === '') {
      UpdateInputList((draft) => {
        draft[0].value = target.value.replace(/\s*/g, '')
      })
      setInput(0)
      setAccount(target.value)
    } else if (target.placeholder === '') {
      UpdateInputList((draft) => {
        draft[1].value = target.value
      })
      setInput(1)
      setUsername(target.value)
    } else if (target.placeholder === ' Email') {
      UpdateInputList((draft) => {
        draft[2].value = target.value.replace(/\s*/g, '')
      })
      setInput(2)
      setEmail(target.value)
    } else if (target.placeholder === '') {
      UpdateInputList((draft) => {
        draft[3].value = target.value.replace(/\s*/g, '')
      })
      setInput(3)
      setPassword(target.value)
    } else if (target.placeholder === '') {
      UpdateInputList((draft) => {
        draft[4].value = target.value.replace(/\s*/g, '')
      })
      setInput(4)
      setCheckPassword(target.value)
    }
  }

  // input
  function disabledAllInput(boolean) {
    if (boolean) {
      for (let i = 0; i < inputs.length; i++) {
        setInput(i, 'disabled')
      }
    } else {
      for (let i = 0; i < inputs.length; i++) {
        setInput(i, '')
      }
    }
  }

  // Button
  async function handleClick() {
    let isValid = true

    //  input value 
    const regexAccount = /^(?=.*[a-zA-Z])(?=.*\d).{1,20}$/
    const regexUsername = /^.{1,50}$/
    const regexEmail = /^[^\s@]+@[^\s@]+\.[^\s@]+$/
    const regexPassword = /^[a-zA-Z0-9]{8,20}$/
    // account 
    if (!regexAccount.test(account)) {
      setInput(0, 'danger', ' 1-20  +  !')
      isValid = false
    }
    // username 
    if (!regexUsername.test(username)) {
      setInput(1, 'danger', ' 1-50  !')
      isValid = false
    }
    // email 
    if (!regexEmail.test(email)) {
      setInput(2, 'danger', ' Email  !')
      isValid = false
    }
    //   ()
    if (password !== checkPassword) {
      setInput(4, 'danger', ' !')
      isValid = false
    }
    // password 
    if (!regexPassword.test(password)) {
      setInput(3, 'danger', ' 8-20  !')
      isValid = false
    }

    if (isValid) {
      // input
      disabledAllInput(true)
      const { success, error } = await registerAccount(
        username,
        account,
        email,
        password,
        checkPassword
      )
      // 
      //  login
      if (success) {
        openModal('', 'success')
        setTimeout(() => {
          navigate('/login')
        }, 1000)
      } else {
        if (error.response.data.message.includes('email')) {
          openModal('Email ', 'danger')
          setInput(2, 'danger', 'Email  !')
          setInput(0)
          setInput(1)
          setInput(3)
          setInput(4)
          isValid = false
        } else if (error.response.data.message.includes('account')) {
          openModal('', 'danger')
          setInput(0, 'danger', ' !')
          setInput(1)
          setInput(2)
          setInput(3)
          setInput(4)
          isValid = false
        }
      }
    }

    if (isValid) {
      // input
      disabledAllInput(true)
    }
  }

  return (
    <StyledContainer className='d-flex flex-column align-items-center'>
      <StyledHeader className='d-flex flex-column align-items-center'>
        <Logo />
        <h3></h3>
      </StyledHeader>

      <StyledBody className='d-flex flex-column align-items-center'>
        {inputList.map((input) => {
          return (
            <AuthInput
              key={input.label}
              type={input.type}
              placeholder={input.placeholder}
              label={input.label}
              status={input.status}
              errorText={input.errorText}
              value={input.value}
              onChange={(event) => {
                handleChange(event)
              }}
            />
          )
        })}
      </StyledBody>

      <StyledFooter className='d-flex flex-column align-items-center'>
        <AuthButton onClick={handleClick}></AuthButton>
        <StyledLinkWrapper className='d-flex justify-content-center'>
          <li>
            <NavLink to='/login'></NavLink>
          </li>
        </StyledLinkWrapper>
      </StyledFooter>
    </StyledContainer>
  )
}
