import axios from 'axios'

const usersURL = 'https://young-lake-63146.herokuapp.com/api/users'

// 
export async function login({ account, password }) {
  try {
    const { data } = await axios.post(`${usersURL}/signin`, {
      account,
      password,
    })
    const { token } = data

    if (token) {
      return { success: true, ...data, token }
    }
  } catch (error) {
    console.error(`Login Failed: ${error}`)
    return { success: false, error }
  }
}

// settin 
export async function settingPage({
  token,
  userid,
  name,
  account,
  email,
  password,
}) {
  try {
    const { data } = await axios.put(
      `${usersURL}/${userid}/setting`,
      {
        account,
        name,
        email,
        password,
      },
      {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      }
    )

    return { success: true, message: data.message }
  } catch (error) {
    let message = ''
    console.error(`Setting Profile Filed: ${error.response.data.message}`)
    if (error.response.data.message.includes('')) {
      message = ' !'
    } else if (error.response.data.message.includes('')) {
      message = ' !'
    }
    return { success: false, message }
  }
}

//  user info
export async function getUser(token, userId) {
  try {
    const { data } = await axios.get(`${usersURL}/${userId}`, {
      headers: {
        Authorization: `Bearer ${token}`,
      },
    })
    return data
  } catch (error) {
    console.error(`Get User Profile Failed: ${error}`)
  }
}

// 
export async function registerAccount(
  username,
  account,
  email,
  password,
  checkPassword
) {
  try {
    const { data } = await axios.post(`${usersURL}`, {
      name: username,
      account,
      email,
      password,
      checkPassword,
    })

    return { success: true, ...data }
  } catch (error) {
    console.error(`Register Failed: ${error}`)
    return { success: false, error }
  }
}

