import { useImmer } from 'use-immer'
import { useState, useEffect } from 'react'
import styled from 'styled-components'
import { useNavigate } from 'react-router-dom'
import { useAuth } from '../context/AuthContext'
import Modal from 'react-modal'
// input 
import { AuthInput } from './AuthInput'
import Notification from '../components/common/Notifications'
// button
import { PillButton } from '../components/common/button.styled'
// token api
import { checkPermission } from '../api/checkPermission'
import { settingPage } from '../api/users'

const StyledForm = styled.form`
  gap: 32px;

  label,
  input {
    padding: 0 10.55px;
  }
  label {
    padding-top: 6px;
  }
`
// Modal
const customStyles = {
  overlay: {
    backgroundColor: 'transparent', // 
  },
  content: {
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'start',
    border: 'none', // 
    background: 'none', // 
    boxShadow: 'none', // 
  },
}

const inputs = [
  {
    label: '',
    placeholder: '',
    type: 'text',
    status: '',
    errorText: '',
    value: '',
  },
  {
    label: '',
    placeholder: '',
    type: 'text',
    status: '',
    errorText: '',
    value: '',
  },
  {
    label: 'Email',
    placeholder: ' Email',
    type: 'text',
    status: '',
    errorText: '',
    value: '',
  },
  {
    label: '',
    placeholder: '',
    type: 'password',
    status: '',
    errorText: '',
    value: '',
  },
  {
    label: '',
    placeholder: '',
    type: 'password',
    status: '',
    errorText: '',
    value: '',
  },
]
export default function Setting() {
  const [inputList, UpdateInputList] = useImmer(inputs)
  const navigate = useNavigate()
  // modal
  const [modalIsOpen, setModalIsOpen] = useState(false)
  const [notiText, setNotiText] = useState('')
  const [notiStatus, setNotiStatus] = useState('')
  // 
  const { currentMember } = useAuth()

  function openModal(notiText, notiStatus) {
    setModalIsOpen(true)
    setNotiText(notiText)
    setNotiStatus(notiStatus)
    setTimeout(() => {
      setModalIsOpen(false)
    }, 1300)
  }

  // token
  useEffect(() => {
    if (currentMember) {
      async function asyncCheckPermission() {
        // token
        const token = localStorage.getItem('token')
        if (token) {
          // token
          const { success } = await checkPermission(token)
          if (!success) {
            // token
            navigate('/login')
          }
        }
      }
      asyncCheckPermission()

      // inputvalue
      UpdateInputList((draft) => {
        draft[0].value = currentMember.account
      })
      UpdateInputList((draft) => {
        draft[1].value = currentMember.name
      })
      UpdateInputList((draft) => {
        draft[2].value = currentMember.email
      })
    }
  }, [navigate, UpdateInputList, currentMember])

  //  input 
  function setInput(num, status, errorText) {
    UpdateInputList((draft) => {
      draft[num].status = status
      draft[num].errorText = errorText
    })
  }

  // input
  function handleChange(event) {
    const target = event.target
    if (target.placeholder === '') {
      UpdateInputList((draft) => {
        draft[0].value = target.value.replace(/\s*/g, '')
      })
      setInput(0)
    } else if (target.placeholder === '') {
      UpdateInputList((draft) => {
        draft[1].value = target.value
      })
      setInput(1)
    } else if (target.placeholder === ' Email') {
      UpdateInputList((draft) => {
        draft[2].value = target.value.replace(/\s*/g, '')
      })
      setInput(2)
    } else if (target.placeholder === '') {
      UpdateInputList((draft) => {
        draft[3].value = target.value.replace(/\s*/g, '')
      })
      setInput(3)
    } else if (target.placeholder === '') {
      UpdateInputList((draft) => {
        draft[4].value = target.value.replace(/\s*/g, '')
      })
      setInput(4)
    }
  }

  // input
  function disabledAllInput(boolean) {
    if (boolean) {
      for (let i = 0; i < inputs.length; i++) {
        setInput(i, 'disabled')
      }
    } else {
      for (let i = 0; i < inputs.length; i++) {
        setInput(i, '')
      }
    }
  }

  // Button
  async function handleClick() {
    let isValid = true

    //  input value 
    const regexAccount = /^(?=.*[a-zA-Z])(?=.*\d).{1,20}$/
    const regexUsername = /^.{1,50}$/
    const regexEmail = /^[^\s@]+@[^\s@]+\.[^\s@]+$/
    const regexPassword = /^[a-zA-Z0-9]{8,20}$/
    // account 
    if (!regexAccount.test(inputList[0].value)) {
      setInput(0, 'danger', ' 1-20  +  !')
      isValid = false
    }
    // username 
    if (!regexUsername.test(inputList[1].value)) {
      setInput(1, 'danger', ' 1-50  !')
      isValid = false
    }
    // email 
    if (!regexEmail.test(inputList[2].value)) {
      setInput(2, 'danger', ' Email  !')
      isValid = false
    }
    //   ()
    if (inputList[4].value !== inputList[3].value) {
      setInput(4, 'danger', ' !')
      isValid = false
    }
    // password 
    if (!regexPassword.test(inputList[3].value)) {
      setInput(3, 'danger', ' 8-20  !')
      isValid = false
    }

    let userid = ''
    let token = ''
    if (isValid) {
      // 1. tokenuserid
      const getToken = localStorage.getItem('token')

      if (getToken) {
        // token
        const { success, data } = await checkPermission(getToken)
        if (success) {
          // token
          userid = data.id
          token = getToken
        } else {
          // token
          setTimeout(() => {
            openModal('Token', 'danger')
          }, 1000)
          navigate('/login')
          isValid = false
        }
      } else {
        // token
        navigate('/login')
        isValid = false
      }
    }
    // 2. useridinput valuesetting api
    if (isValid) {
      disabledAllInput(true)
      const { success, message } = await settingPage({
        token,
        userid,
        name: inputList[1].value,
        account: inputList[0].value,
        password: inputList[3].value,
        email: inputList[2].value,
      })
      if (success) {
        navigate('/setting')
        openModal(`${message}`, 'success')
      } else {
        openModal(`${message}`, 'danger')
      }
      disabledAllInput()
    }
  }

  return (
    <>
      <StyledForm
        className='d-flex flex-column p-4'
        onSubmit={(e) => {
          e.preventDefault()
        }}
      >
        {inputList.map((input) => {
          return (
            <AuthInput
              key={input.label}
              type={input.type}
              placeholder={input.placeholder}
              label={input.label}
              status={input.status}
              errorText={input.errorText}
              value={input.value}
              onChange={(event) => {
                handleChange(event)
              }}
            />
          )
        })}
        <div className='buttonWrapper d-flex justify-content-end'>
          <PillButton
            type='submit'
            filled='true'
            large='true'
            onClick={handleClick}
          >
            
          </PillButton>
        </div>
      </StyledForm>

      <Modal isOpen={modalIsOpen} style={customStyles}>
        <Notification status={notiStatus}>{notiText}</Notification>
      </Modal>
    </>
  )
}
