import { useState } from 'react'
import { useImmer } from 'use-immer'
import { NavLink, useNavigate } from 'react-router-dom'
import styled from 'styled-components'

// logo
import { Logo } from '../assets/icons'
// button
import { AuthButton } from './common/button.styled'
// container
import { AuthContainer, AuthInputContainer } from './common/auth.styled'
// input 
import { AuthInput } from './AuthInput'
// api
// import { login } from '../api/users'
import { useAuth } from '../context/AuthContext'

// auth container
const StyledContainer = styled(AuthContainer)`
  max-width: 356px;
  padding: 0;

  svg {
    margin-bottom: 24px;
  }

  button {
    margin-bottom: 22px;
  }
`
// input container
const StyledInputContainer = styled(AuthInputContainer)`
  margin: 40px 0;
  gap: 32px;

  label,
  input {
    padding: 0 10.55px;
  }
  label {
    padding-top: 6px;
  }
`
// Link
const StyledLink = styled.ul`
  width: 100%;
  gap: 12px;

  span a {
    line-height: 24px;
  }

  a {
    text-decoration: none;
    color: var(--primary);
    border-bottom: 1px solid var(--primary);
  }
`

const inputs = [
  {
    label: '',
    placeholder: '',
    type: 'text',
    status: '',
    errorText: '',
    value: '',
  },
  {
    label: '',
    placeholder: '',
    type: 'password',
    status: '',
    errorText: '',
    value: '',
  },
]

// Login
export default function Login({ openModal }) {
  const [account, setAccount] = useState('') //  value
  const [password, setPassword] = useState('') //  value
  const [inputList, UpdateInputList] = useImmer(inputs)
  const navigate = useNavigate()
  const { login } = useAuth()

  //  input 
  function setInput(num, status, errorText) {
    UpdateInputList((draft) => {
      draft[num].status = status
      draft[num].errorText = errorText
    })
  }

  // input
  function handleChange(event) {
    const target = event.target
    if (target.type === 'text') {
      UpdateInputList((draft) => {
        draft[0].value = target.value.replace(/\s*/g, '')
      })
      setInput(0)
      setAccount(target.value)
    } else if (target.type === 'password') {
      UpdateInputList((draft) => {
        draft[1].value = target.value.replace(/\s*/g, '')
      })
      setInput(1)
      setPassword(target.value)
    }
  }

  // input
  function disabledAllInput(boolean) {
    if (boolean) {
      for (let i = 0; i < inputs.length; i++) {
        setInput(i, 'disabled')
      }
    } else {
      for (let i = 0; i < inputs.length; i++) {
        setInput(i, '')
      }
    }
  }

  // Button
  async function handleClick() {
    let isValid = true

    //  accountpassword 
    const regexAccount = /^(?=.*[a-zA-Z])(?=.*\d).{1,20}$/
    const regexPassword = /^[a-zA-Z0-9]{8,20}$/

    // account 
    if (!regexAccount.test(account)) {
      setInput(0, 'danger', ' 1-20  +  !')
      isValid = false
    }
    // password
    if (!regexPassword.test(password)) {
      setInput(1, 'danger', ' 8-20  !')
      isValid = false
    }

    if (isValid) {
      // input
      disabledAllInput(true)
      //  successauthToken 

      const { success, error } = await login({ account, password })
      //  authToken  localStorage
      //  homePage
      if (success) {
        openModal('', 'success')
        setTimeout(() => {
          navigate('/home')
        }, 1000)
      } else {
        if (error.response.data.message.includes('')) {
          openModal('', 'danger')
          setInput(0, 'danger', ' !')
          setInput(1)
          isValid = false
        } else if (error.response.data.message.includes('')) {
          openModal('', 'danger')
          setInput(1, 'danger', ' !')
          setInput(0)
          isValid = false
        }
      }

      if (isValid) {
        // input
        disabledAllInput()
      }
    }
  }


  return (
    <StyledContainer>
      <Logo />
      <h3> Alphitter</h3>
      <StyledInputContainer as='form' className='d-flex flex-column'>
        {inputList.map((input) => {
          return (
            <AuthInput
              key={input.label}
              type={input.type}
              placeholder={input.placeholder}
              label={input.label}
              status={input.status}
              errorText={input.errorText}
              value={input.value}
              onChange={(event) => {
                handleChange(event)
              }}
            />
          )
        })}
      </StyledInputContainer>
      <AuthButton
        onClick={() => {
          handleClick()
        }}
      >
        
      </AuthButton>
      <StyledLink className='d-flex justify-content-end'>
        <li>
          <NavLink to='/register'></NavLink>
        </li>
        <span></span>
        <li>
          <NavLink to='/admin/login'></NavLink>
        </li>
      </StyledLink>
    </StyledContainer>
  )
}
